.promo {
  background-color: #F9F9F9;

  .container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 50px;
    padding-top: 50px;

    @media(min-width: 768px) {
      padding-bottom: 130px;
      padding-top: 130px;
    }
  }

  &__text-block {
    margin-right: auto;
    margin-bottom: 75px;
    margin-left: auto;
    text-align: center;

    @media(min-width: 768px) {
      margin-bottom: 50px;
    }

    @media(min-width: 1024px) {
      margin-right: 0;
      margin-bottom: 0;
      margin-left: 0;
      text-align: left;
    }
  }

  &__title {
    margin-top: 0;
    margin-bottom: 22px;
    font-weight: 900;
    font-size: 40px;
    font-size: 30px;
    text-transform: uppercase;
    background: linear-gradient(90deg, #E30D42 1%, #444242 50%);
    background-clip: text;
    -webkit-text-fill-color: transparent;

    @media(min-width: 768px) {
      font-size: 40px;
    }

    @media (min-width: 1024px) {
      background: linear-gradient(90deg, #E30D42 1%, #444242 20%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }

  }

  &__text {
    max-width: 525px;
    margin-top: 0;
    margin-bottom: 55px;
  }

  &__button {
    position: relative;
    padding: 15px 70px 15px 45px;
    font-weight: 500;
    font-size: 20px;
    color: #FFFFFF;
    background-color: #201D54;
    border: none;
    border-radius: 100px;

    &:hover {
      background-color: #2B276F;
    }

    &:active {
      color: #CAC9DB;
    }

    &::after {
      content: url("../img/icons/button-arrow.svg");
      position: absolute;
      top: 17px;
      right: 38px;
      width: 22px;
      height: 22px;
    }
  }

  &__img-wrapper {
    position: relative;
    z-index: 2;
    display: flex;
    margin-right: auto;
    margin-left: auto;
    text-align: center;
    background-image: url("../img/icons/card-bg.svg");
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: 100%;


    &::before {
      content: '';
      position: absolute;
      top: -35px;
      left: -28.5px;
      z-index: -1;
      width: 250px;
      height: 250px;
      background-color: #FFD610;
      border-radius: 100%;
    }

    &::after {
      content: '';
      position: absolute;
      top: -35px;
      right: -33px;
      z-index: -1;
      width: 250px;
      height: 250px;
      background-color: #F7EBB3;
      border-radius: 100%;
    }
  }

  &__img {
    z-index: 2;
    width: 300px;
    height: 194px;
    margin-right: auto;
    margin-left: auto;
    text-align: center;
    border-radius: 5px;
    box-shadow: 0px 0px 22px 0px rgba(0, 0, 0, 0.1);

    @media(min-width: 768px) {
      width: 402px;
      height: 256px;
    }
  }
}
